<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul
          class="cart-list"
          v-for="(cart, index) in cartInfoList"
          :key="cart.id"
        >
          <li class="cart-list-con1">
            <input
              type="checkbox"
              name="chk_list"
              :checked="cart.isChecked === 1"
              @change="updateChecked(cart,$event)"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl" />
            <div class="item-msg">{{ cart.skuName }}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con5">
            <a
              href="javascript:void(0)"
              class="mins"
              @click="handler('minus', -1, cart)"
              >-</a
            >
            <input
              autocomplete="off"
              type="text"
              :value="cart.skuNum"
              minnum="1"
              class="itxt"
              @change="handler('change', $event.target.value * 1, cart)"
            />
            <a
              href="javascript:void(0)"
              class="plus"
              @click="handler('add', 1, cart)"
              >+</a
            >
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuPrice * cart.skuNum }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)">删除</a>
            <br />
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" :checked="isAllCheck&&cartInfoList.length>0" @change="updataAllCartChecked"/>
        <span>全选</span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择 <span>0</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ totalPrice }}</i>
        </div>
        <div class="sumbtn">
          <a class="sum-btn" @click="$router.push('/trade')">结算</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
// 按需引入lodash
import throttle from "lodash";
  export default {
    name: 'ShopCart',
    mounted() {
      // 获取个人购物车数据
      this.getData()
    },
    methods: {
      // 获得个人购物车数据
      getData(){
        this.$store.dispatch("getCartList")
      },
      // 修改某一个产品的个数 使用lodash防抖
      // _.debounce(func, [wait=0], [options=]) //func(Function): 要防抖动的函数。[wait=0](number): 需要延迟的毫秒数。[options=](Object): 选项对象
      handler: _.debounce(async function (type,disNum,cart) {
        // type:区分这三个元素
        // disNum:+变化量(1)、-变化量(-1)、input最终的个数(不是变化量)
        // cart:哪一个产品(身上有id)
        // 向服务器发起请求，修改数量
        switch(type){
          // 加号
          case "add":
            // 带给服务器变化量disNum为正1
              disNum = 1
            break;
          case "minus":
              // 判断产品个数大于1，才能传给服务器的变化量disNum为负1。相反，disNum为0，不增不减
              disNum = cart.skuNum >1 ? -1 : 0  
            break;
          case "change":
              // 用户输入最终的结果是非法的(比如带有汉字或者英文||出现负数)，带给服务器的变化量disNum应该为0
              // 正常情况（如果输入小数，就取整）带给服务器的是变化的量 用户输入的数字 - 产品的起始数据
              disNum = isNaN(disNum)||disNum<1 ? 0 : parseInt(disNum)-cart.skuNum
            break;
        }
        // 派发action
        // try代表修改成功
        try{
          await this.$store.dispatch("addOrUpdateShopCart",{skuId:cart.skuId,skuNum:disNum})
          // 再一次获取服务器最新的数据进行展示
          this.getData()
        }catch(error){

        }
      }, 500),
      // 删除购物车某一个产品
      async deleteCartById(cart){
        // 派发action
        // try代表修改成功
        try{
          await this.$store.dispatch("deleteCartListBySkuId",cart.skuId)
          this.getData()
        }catch(error){
          alert(error.message)
        }
      },
      // 修改购物车某一个产品勾选的状态
      async updateChecked(cart,event){
        // 带给服务器的参数不能是布尔值，$event.target.checked这里需要转换成为0或者1
        try{
          let checked = event.target.checked ? "1" : "0"
          await this.$store.dispatch("UpdateCheckedById",{skuId:cart.skuId,isChecked:checked})
          this.getData()
        }catch(error){
          alert(error.message)
        }
      },
      // 删除全部选中的产品  不需要传参
      async deleteAllCheckedCart(){
        try {
          // 派发action
          await this.$store.dispatch("deleteAllCheckedCart")
          this.getData()
        } catch (error) {
          alert(error.message)
        }
      },
      // 修改购物车中全部商品选中的状态
      async updataAllCartChecked(event){
        try {
          let isChecked = event.target.checked?"1":"0"
          // 派发action
          await this.$store.dispatch("updateAllCartIsChecked",isChecked)
          this.getData()
        } catch (error) {
          alert(error.message)
        }
      },
      
    },
    computed: {
      ...mapGetters(["cartList"]),
      // 购物车数据
      cartInfoList(){
        return this.cartList.cartInfoList || []
      },
      // 计算产品购买的总价
      totalPrice(){
        // 遍历购物车数据
        let sum = 0 
        this.cartInfoList.forEach(item => {
          sum = sum + item.skuNum * item.skuPrice
        });
        return sum
      },
      // 计算全选情况
      isAllCheck(){
        // 遍历数组原理，只要全部元素isChecked属性都为1----就是真true，有一个属性不为1，就是假false
        return this.cartInfoList.every(item => item.isChecked === 1)
      },
    },
  }
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con4 {
          width: 10%;
        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
            cursor: pointer;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>